import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { FaSortAmountDown } from 'react-icons/fa';
// import axios from 'axios'
import PrvyRiadokTabulky from './PrvyRiadokTabulky';
import FormatNumber from './FormatNumber'
import './css/TabulkaSvet.css'



function SvetTabulka(props) {
    const [ikonka, setIkonka] = useState('cases')
    // const [sipkyDole, setSipkyDole] = useState(true)
    const [kontinent, setKontinent] = useState("Svet")
    // const [dataKontinenty, setDataKontinenty] = useState()
    // const [sipkaKontinenty, setSipkaKontinenty] = useState({
    //     transition: 'all .2s ease-in',
    //     marginBottom: '-3px',
    //     marginLeft: '3px',
    // })
    // const [styleForRollDown, setStyleForRollDown] = useState({
    //     position: 'absolute',
    //     left: '-1000px',
    //     transition: 'all .2s ease-in',
    //     fontSize: '15px',
    // })

    function zmenIkonku(key) {
        if (key === ikonka) {
            setIkonka(key + "2")
        }
        else { setIkonka(key) }
    }

    const myStyle2 = {
        transform: "rotateX(180deg)",
    }


    // useEffect(() => {
    //     axios.get(`https://disease.sh/v2/continents`)
    //         .then(res => {
    //             setDataKontinenty([res.data])
    //             // console.log(res.data)
    //         })


    // }, [])



    function myFunction() {
        var input, filter, table, tr, td, i, txtValue;
        input = document.getElementById("myInput");
        filter = input.value.toUpperCase();
        table = document.getElementById("myTable");
        tr = table.getElementsByTagName("tr");
        for (i = 0; i < tr.length; i++) {
            td = tr[i].getElementsByTagName("td")[0];
            if (td) {
                txtValue = td.textContent || td.innerText;
                if (txtValue.toUpperCase().indexOf(filter) > -1) {
                    tr[i].style.display = "";
                } else {
                    tr[i].style.display = "none";
                }
            }
        }
    }


    // function rollDownContintinents() {
    //     setSipkyDole(!sipkyDole)
    //     if (sipkyDole) {
    //         setSipkaKontinenty({
    //             transform: "rotate(180deg)",
    //             transition: 'all .2s ease-in',
    //             marginBottom: '-3px',
    //             marginLeft: '3px',
    //         })
    //         setStyleForRollDown({
    //             background: 'rgb(234, 234, 234)',
    //             fontSize: '15px',
    //             transition: 'all .2s ease-in',
    //         })
    //     }
    //     else {
    //         setStyleForRollDown({
    //             position: 'absolute',
    //             left: '-1000px',
    //             transition: 'all .2s ease-in',
    //             fontSize: '15px',
    //         })

    //         setSipkaKontinenty({
    //             transform: "rotate(0deg)",
    //             transition: 'all .2s ease-in',
    //             marginBottom: '-3px',
    //             marginLeft: '3px',
    //         })
    //     }
    // }

    // function functionForWorld(id) {
    //     return (<>
    //         <tr key={id} style={{ background: 'rgb(234, 234, 234)' }} >
    //             <td style={{ width: '100px' }} onClick={rollDownContintinents}>
    //                 Celý svet<span >
    //                 </span>
    //             </td>
    //             {props ? <>
    //                 <td>{FormatNum(props.mainData[0].cases)}</td>
    //                 <td>{FormatNum(props.mainData[0].active)}</td>
    //                 <td style={{ background: ' rgb(254, 255, 194, .4)' }}>+{FormatNum(props.mainData[0].todayCases)} </td>
    //                 <td>{FormatNum(props.mainData[0].deaths)}</td>
    //                 <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+{FormatNum(props.mainData[0].todayDeaths)} </td>
    //                 <td style={{ fontWeight: 'bold', color: 'green' }}>{FormatNum(props.mainData[0].recovered)}</td>
    //             </>
    //                 : ''}
    //         </tr>
    //     </>
    //     )
    // }
    function greenBackgroundforTR(active) {
        if (active)
            return null
        else
            return ({ background: 'rgb(190, 245, 204)' })
    }

    const KontinentZmena = (nazov) => {
        setKontinent(nazov)
    }

    return (
        <>
            <input type="text" id="myInput" onKeyUp={myFunction} placeholder="Hľadať.." className="inputSearch" />
            <div className="kontinenty">
                <button onClick={() => KontinentZmena("Svet")}>
                    Svet
            </button>
                <button onClick={() => KontinentZmena("Europe")}>
                    Europe
            </button>
                <button onClick={() => KontinentZmena("North America")}>
                    North America
            </button>
                <button onClick={() => KontinentZmena("Asia")}>
                    Asia
            </button>
                <button onClick={() => KontinentZmena("South America")}>
                    South America
            </button>
                <button onClick={() => KontinentZmena("Africa")}>
                    Africa
            </button>
                <button onClick={() => KontinentZmena("Australia/Oceania")}>
                    Australia/Oceania
            </button>
            </div>
            <div className="divTable" >
                <table id="myTable">
                    <thead>
                        <tr>
                            <th onClick={() => (props.sortBy(`tests`), zmenIkonku(`tests`))} >
                                Krajina
                            {ikonka === 'tests'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'tests2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('cases'), zmenIkonku('cases'))} >
                                Počet nakazených
                            {ikonka === 'cases'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'cases2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('active'), zmenIkonku('active'))} >
                                Aktívne prípady
                            {ikonka === 'active'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'active2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('todayCases'), zmenIkonku('todayCases'))} >
                                Nové prípady
                            {ikonka === 'todayCases'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'todayCases2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('deaths'), zmenIkonku('deaths'))} >
                                Počet úmrtí
                            {ikonka === 'deaths'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'deaths2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('todayDeaths'), zmenIkonku('todayDeaths'))} >
                                Nové úmrtia
                            {ikonka === 'todayDeaths'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'todayDeaths2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('recovered'), zmenIkonku('recovered'))} >
                                Počet vyliečených
                            {ikonka === 'recovered'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'recovered2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                        </tr>
                    </thead>
                    <tbody>

                        <PrvyRiadokTabulky kontinent={kontinent} mainData={props.mainData} />
                        {props.mainData ?
                            kontinent === "Svet" ?
                                // <tr key={-1} style={{ background: 'rgb(234, 234, 234)' }} >
                                //     <td style={{ width: '100px' }} onClick={rollDownContintinents}>
                                //         Celý svet<span >
                                //         </span>
                                //     </td>
                                //     <td>{FormatNum(props.mainData[0].cases)}</td>
                                //     <td>{FormatNum(props.mainData[0].active)}</td>
                                //     <td style={{ background: ' rgb(254, 255, 194, .4)' }}>+{FormatNum(props.mainData[0].todayCases)} </td>
                                //     <td>{FormatNum(props.mainData[0].deaths)}</td>
                                //     <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+{FormatNum(props.mainData[0].todayDeaths)} </td>
                                //     <td style={{ fontWeight: 'bold', color: 'green' }}>{FormatNum(props.mainData[0].recovered)}</td>
                                // </tr>
                                null
                                :
                                // console.log(dataKontinenty[0][0].cases) 
                                null
                            : null}

                        {props.countriesData.map((item, id) => (
                            <>
                                {item.continent === kontinent ?
                                    <tr key={id} style={greenBackgroundforTR(item.active)}>
                                        <td style={{ width: '100px' }}>
                                            {item.country === 'Slovakia' ?
                                                <div style={{ fontWeight: 'bold', textDecoration: 'underline' }}>
                                                    <Link to='/Covid/CovidSK'>{item.country}</Link>
                                                </div>
                                                : item.country
                                            }
                                            {item.tests > 0 ?
                                                <div className="firstCase">Počet testov:
                                                    <div className="firstCaseNumber">
                                                        <FormatNumber prop={item.tests} />
                                                    </div>
                                                </div>
                                                : null}
                                        </td>
                                        <td><FormatNumber prop={item.cases} /></td>
                                        <td>{item.active ? <FormatNumber prop={item.active} /> : "0"}</td>
                                        {item.todayCases > 0 ?
                                            <td style={{ background: ' rgb(254, 255, 194)' }}>+<FormatNumber prop={item.todayCases} /> </td>
                                            : <td></td>
                                        }
                                        <td><FormatNumber prop={item.deaths} /></td>
                                        {item.todayDeaths > 0 ?
                                            <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+<FormatNumber prop={item.todayDeaths} /> </td>
                                            : <td></td>
                                        }
                                        <td style={{ fontWeight: 'bold', color: 'green' }}><FormatNumber prop={item.recovered} /></td>
                                    </tr>
                                    :
                                    kontinent === "Svet" ?
                                        <tr key={id} style={greenBackgroundforTR(item.active)}>
                                            <td style={{ width: '100px' }}>
                                                {item.country === 'Slovakia' ?
                                                    <div style={{ fontWeight: 'bold', textDecoration: 'underline' }}>
                                                        <Link to='/Covid/CovidSK'>{item.country}</Link>
                                                    </div>
                                                    : item.country
                                                }
                                                {item.tests > 0 ?
                                                    <div className="firstCase">Počet testov: <div className="firstCaseNumber"><FormatNumber prop={item.tests} /></div></div>
                                                    : null}
                                            </td>
                                            <td><FormatNumber prop={item.cases} /></td>
                                            {/* <td><FormatNumber prop={item.active} /> {item.active ? null : "0a"}</td> */}
                                            <td>{item.active ? <FormatNumber prop={item.active} /> : "0"}</td>

                                            {item.todayCases > 0 ?
                                                <td style={{ background: ' rgb(254, 255, 194)' }}>+<FormatNumber prop={item.todayCases} /> </td>
                                                : <td></td>
                                            }
                                            <td><FormatNumber prop={item.deaths} /></td>
                                            {item.todayDeaths > 0 ?
                                                <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+<FormatNumber prop={item.todayDeaths} /> </td>
                                                : <td></td>
                                            }
                                            <td style={{ fontWeight: 'bold', color: 'green' }}><FormatNumber prop={item.recovered} /></td>
                                        </tr> : null}
                            </>
                        ))}
                    </tbody>
                </table>
            </div>
        </>
    )
}

export default SvetTabulka